                    .mainPresBlock_text{
                        position: relative;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 40px;
                        line-height: 120%; 
                        text-align: center;    
                        text-align: center;
                    }
                    &:last-child{
                        width: 100%;
                        display: flex;
                        flex-wrap: nowrap;
                        align-items: center;
                        justify-content: center;
                        position: relative;
                        margin-top: 1px;
                        
                    }
                    span{
                        margin-left: 0px;
                        display: inline-block;
                        width: 133px;
                        max-height: 33px;
                        min-height: 36px;
                        color: #E6007E;
                        font-style: normal;
                        font-weight: 400;
                        font-size: 40px;
                        line-height: 64%; 
                        text-align: center; 
                        padding: 0 0px;
                        transform: skew(-9deg);
                        background: #fff;
                        span{
