        .mainPresBlockPictureColumn{
            padding: 116px 0 0 0;
            position: absolute;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: auto;
            height: 100%;
           
            .mainPresBlockPictureRow{
                height: 25%;
                display: flex;
                position: relative;
                gap: 48px;
